.cell{
  width: 100%;
  height: 100%;
  user-select: none;
  transition: transform 1s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cell svg{
  /* width: 20px;
  height: 20px; */
  width: 100%;
  height: 100%;
}
.cell.start{
  color: green;
}
.cell.end{
  color: red;
}
.cell.checkpoint{
  width: 100%;
  height: 100%;
  color: orange;
  background-image: url('../images/checkpoint.svg');
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  /* animation: beat 0.3s 0s; */
}
.cell.wall{
  background: #0c3547;
  transform: scale(1.1);
  animation: grow 0.3s 0s;
}
.cell.weight-vl{
  width: 100%;
  height: 100%;
  background-image: url('../images/lock-vl.svg');
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  animation: grow 0.3s 0s;
}
.cell.weight-l{
