}
.cell.visited_to_checkpoint{
  animation: visited_to_checkpoint 1.4s 0s;
  background-color: rgb(197, 114, 255);
  transform: scale(1.02);
}
.cell.visited{
  animation: visited 1.4s 0s;
  background-color: rgb(70, 181, 255);
  transform: scale(1.02);
}
.cell.path{
  background-color: rgb(255, 255, 91);
  animation: grow 0.3s 0s;
  transform: scale(1.1);
}

@keyframes grow{
  from{
    transform: scale(0.2);
  }
  to{
    transform: scale(1.2);
  }
}
@keyframes beat{
  from{
    transform: scale(0.5);
  }
  40%{
    transform: scale(1.2);
  }
  80%{
    transform: scale(0.7);
  }
  to{
    transform: scale(1);
  }
}
@keyframes visited{
  0%{
    transform: scale(0.3);
    border-radius: 50%;
    background-color: #003483;
  }
  80%{
    background-color: rgb(122, 255, 182);
    transform: scale(1.1);
  }
  100%{
    transform: scale(1);
    border-radius: 0%;
    background-color: rgb(70, 181, 255);
  }
}
@keyframes visited_to_checkpoint{
  0%{
    transform: scale(0.3);
    border-radius: 50%;
    background-color: rgb(143, 8, 136);
  }
  80%{
    background-color: rgb(231, 74, 223);
    transform: scale(1.1);
  }
  100%{
    transform: scale(1);
    border-radius: 0%;
    background-color: rgb(197, 114, 255);
  }
}